.footer-container {
    background-color: rgb(var(--background));
    padding: 2rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-links {
    width: 100%;
    max-width: 1300px;
    display: flex;
    justify-content: center;
}

.footer-link-wrapper {
    display: flex;
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2 {
    color: rgb(var(--primary));
    margin-bottom: 16px;
}

.footer-link-items a {
    color: rgb(var(--primary));
    text-decoration: none;
    margin-bottom: 0.5rem;
    border-bottom: 1px solid transparent;
    transition: 0.3s ease-out;
}

.footer-link-items a:hover {
    border-bottom: 1px solid rgb(var(--primary));
}

.social-icon-link {
    color: rgb(var(--primary));
    font-size: 24px;
}

.social-media {
    max-width: 1300px;
    width: 100%;
}

.social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1300px;
    margin: 40px auto 0 auto;
}

.social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 375px;
}

.social-logo {
    color: rgb(var(--primary));
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

.website-rights {
    color: rgb(var(--primary));
    margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
    .footer-links {
        padding-top: 2rem;
    }

    .footer-link-wrapper {
        flex-direction: column;
    }

    .social-media-wrap {
        flex-direction: column;
    }
}