:root {
    --btn-background: rgb(var(--primary));
    --btn-color: rgb(var(--background));
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.btn-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
}

.btn--outline {
    background-color: transparent;
    color: var(--btn-background);
    padding: 8px 20px;
    border: 1px solid var(--btn-background);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: rgb(var(--primary));
    padding: 14px 20px;
    border: 1px solid rgb(var(--primary));
    transition: all 0.3s ease-out;
}

.btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background: var(--btn-background);
    color: var(--btn-color);
}

.btn--color-invert {
    color: var(--btn-color);
    border: 1px solid var(--btn-color);
}

.btn--color-invert:hover {
    color: var(--btn-background);
    background-color: var(--btn-color);
}

/* Theme Button */

.theme-button {
    display: flex;
    padding: 0.25em 1em;
    bottom: 0;
    position: fixed;
    cursor: pointer;
    background-color: rgb(var(--background));
    border-top-right-radius: 24px;
    border: 1px solid rgb(var(--primary));
    transition: all 0.1s ease-in;
}

.theme-button:hover {
    border: 2px solid rgb(var(--primary));
}